<template>
    <div id="index-main">
        <div v-if="viewDefault">
            <h3>越过长城，走向世界</h3>
            <p>Across the Great Wall we can reach every corner in the world</p>
            <div class="index-main-btn">
                <router-link to='login' tag='span'>登陆 </router-link>/
                <router-link to='register' tag='span'> 注册</router-link>
            </div>
        </div>
        <div v-if="!viewDefault">
            <h3>{{email}}</h3>
            <p>You are logged in</p>
            <div class="index-main-btn">
                <router-link to='user' tag='span'>进入控制面板 </router-link>/
                <span @click='signOut'> 退出登陆</span>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    data() {
            return {
                viewDefault: true,
                email: ''
            }
        },
        created() {
            //检测本地
            let jsonInfo = localStorage.getItem('acrossw-info')
            let token = localStorage.getItem('acrossw-token')

            if (jsonInfo && token) {

                let info = JSON.parse(jsonInfo);

                this.viewDefault = false;
                this.email = info.email;
            }
        },
        methods: {
            signOut () {
                this.$message({
                    message: '已退出登录',
                    type: 'success',
                    onClose: c => {
                        this.$router.push('')
                    }
                });
            }
            // signOut: r => {
            //     localStorage.removeItem('acrossw-info')
            //     localStorage.removeItem('acrossw-token')
            //     this.$message({
            //         message: '已退出登录',
            //         type: 'success',
            //         onClose: c => {
            //             this.$router.push('/')
            //         }
            //     });
            // }
        }
}
</script>



